"use clien"
import React, { useState } from "react";
import {
  Grid,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Button,
  Pagination,
} from "@mui/material";

const agents = [
  {
    id: 1,
    name: "Tom Willson",
    role: "Marketing",
    phone: "91 456 9***",
    email: "infoagent@example.com",
    image: "https://via.placeholder.com/150", // Replace with your image URL
  },
  {
    id: 2,
    name: "Thomas Joshe",
    role: "Sales",
    phone: "981 123 ***",
    email: "infoagent@example.com",
    image: "https://via.placeholder.com/150", // Replace with your image URL
  },
  // Add more agents as needed
];

const ITEMS_PER_PAGE = 4;

export default function AgentsGridView() {
  const [page, setPage] = useState(1);

  const handlePageChange = (event, value) => {
    setPage(value);
  };

  const paginatedAgents = agents.slice(
    (page - 1) * ITEMS_PER_PAGE,
    page * ITEMS_PER_PAGE
  );

  return (
    <Box sx={{ padding: "20px" }}>
      <Grid container spacing={3}>
        {paginatedAgents.map((agent) => (
          <Grid item xs={12} sm={6} md={4} key={agent.id}>
            <Card>
              <CardMedia
                component="img"
                height="140"
                image={agent.image}
                alt={agent.name}
              />
              <CardContent>
                <Typography variant="h6" component="div">
                  {agent.name}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {agent.role}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Phone: {agent.phone}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Email: {agent.email}
                </Typography>
                <Button size="small" variant="contained" style={{ marginTop: "10px" }}>
                  View My Listings
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
      <Box sx={{ marginTop: "20px", display: "flex", justifyContent: "center" }}>
        <Pagination
          count={Math.ceil(agents.length / ITEMS_PER_PAGE)}
          page={page}
          onChange={handlePageChange}
        />
      </Box>
    </Box>
  );
}
